import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        EXPECTED_MONTHLY_HR: 160,
        ANNUAL_HR: 2080,

        project1EndClientRate: 0,
        vmsFeeProject1percentage: 0,
        rateAfterVmsFeeProject1: 0,
        dorProject1Percentage: 0,
        rateAfterDorProject1: 0,
        loadProject1: 0,
        billHumetisProject1: 0,
        sugPayHY1: 0,
        sugPayHY2: 0,
        sugPayHY3: 0,
        sugPayUSC: 0,
        payRatePercentage1: 0,
        payConsulPerHr1: 0,
        offsetProject1: 0,
        monthlyHrProject1: 0,
        payConsulPerYr1: 0,

        project2EndClientRate: 0,
        vmsFeeProject2percentage: 0,
        rateAfterVmsFeeProject2: 0,
        dorProject2Percentage: 0,
        rateAfterDorProject2: 0,
        loadProject2: 0,
        billHumetisProject2: 0,
        payRatePercentage2: 0,
        payConsulPerHr2: 0,
        offsetProject2: 0,
        monthlyHrProject2: 0,
        payConsulPerYr2: 0,

        consultAnnualPay1: 0,
        consultAnnualPay2: 0,
        consultPTO: 20,
        totalConsultPay: 0,


        fixedAnnualPay1: 0,
        fixedAnnualPay2: 0,
        qtrFlexFund1: 0,
        qtrFlexFund2: 0,
        customFlex1: 10,
        customFlex2: 50,
        medicalPlan: 4800,
        pf: 600,
        noOfPTO: 20,
        totalCTC: 0,

        LCAFixedAnnualPay: 80000,
        LCAQtrFlexFund: 0,
        LCACustomFlex: 50,
        LCAMedicalPlan: 4800,
        LCAPf: 600,
        LCANoOfPTO: 20,
        LCATotalCTC: 0,

        dataTableConsult: [],
        dataTableSalary: [],
        dataTableSalaryLCA: [],

        totalEarningOpt:0,
        earnedOver:0,
        allowedNonTax:0,

        LCATotalEarningOpt:0,
        LCAEarnedOver:0,
        LCAAllowedNonTax:0,

        username:'',
        password:'',
        LoggendIn:false,


    },

    getters: {},

    mutations: {
        UPDATE_VMS(state, project) {
            if (project === 1) {
                state.rateAfterVmsFeeProject1 = parseFloat(parseFloat(state.project1EndClientRate) - (parseFloat(state.project1EndClientRate) * (parseFloat(state.vmsFeeProject1percentage) / 100))).toFixed(2)
            }

            if (project === 2) {
                state.rateAfterVmsFeeProject2 = parseFloat(parseFloat(state.project2EndClientRate) - (parseFloat(state.project2EndClientRate) * parseFloat(state.vmsFeeProject2percentage / 100))).toFixed(2)
            }
        },
        UPDATE_DOR(state, project) {
            if (project === 1) {
                state.rateAfterDorProject1 = parseFloat(state.rateAfterVmsFeeProject1 - (state.rateAfterVmsFeeProject1 * (state.dorProject1Percentage / 100))).toFixed(2)
            }

            if (project === 2) {
                state.rateAfterDorProject2 = parseFloat(parseFloat(state.rateAfterVmsFeeProject2) - parseFloat(state.rateAfterVmsFeeProject2 * parseFloat((state.dorProject2Percentage / 100)))).toFixed(2)
            }
        },

        UPDATE_LOAD(state, project) {
            if (project === 1) {
                state.billHumetisProject1 = parseFloat(state.rateAfterDorProject1 - state.loadProject1).toFixed(2)
                state.sugPayHY1 = Math.round(80 - (3.5 / state.billHumetisProject1) * 100)
                state.sugPayHY2 = Math.round(80 - (1 / state.billHumetisProject1) * 100)
                state.sugPayHY3 = Math.round(80 - (0.5 / state.billHumetisProject1) * 100)
                state.sugPayUSC = 80
            }

            if (project === 2) {
                state.billHumetisProject2 = parseFloat(parseFloat(state.rateAfterDorProject2) - parseFloat(state.loadProject2)).toFixed(2)
            }
        },

        UPDATE_PAY_RATE(state, project) {
            if (project === 1) {
                state.payConsulPerHr1 = parseFloat(parseFloat(state.billHumetisProject1) * parseFloat(parseFloat(state.payRatePercentage1) / 100)).toFixed(2)
            }

            if (project === 2) {
                state.payConsulPerHr2 = parseFloat(parseFloat(state.billHumetisProject2) * parseFloat(parseFloat(state.payRatePercentage2) / 100)).toFixed(2)
            }
        },

        UPDATE_PAY_RATE_YR(state, project) {
            if (project === 1) {
                state.monthlyHrProject1 = parseFloat((state.offsetProject1 / 100) * state.EXPECTED_MONTHLY_HR).toFixed(2)
                state.payConsulPerYr1 = parseFloat(state.payConsulPerHr1 * state.ANNUAL_HR * (state.offsetProject1 / 100)).toFixed(2)
            }

            if (project === 2) {
                state.monthlyHrProject2 = parseFloat((state.offsetProject2 / 100) * state.EXPECTED_MONTHLY_HR).toFixed(2)
                state.payConsulPerYr2 = parseFloat(state.payConsulPerHr2 * (state.monthlyHrProject2 * 12) * (state.offsetProject1 / 100)).toFixed(2)
            }

            state.consultAnnualPay1 = parseFloat(parseFloat(state.payConsulPerHr1) * (2080 - (state.consultPTO * 8))).toFixed(2)
            state.consultAnnualPay2 = parseFloat(state.payConsulPerYr2).toFixed(2)
            state.totalConsultPay = parseFloat(parseFloat(state.consultAnnualPay1) + parseFloat(state.consultAnnualPay2)).toFixed(2)
        },

        UPDATE_SALARY_EMP(state) {
            state.fixedAnnualPay1 = 0;
            state.qtrFlexFund1 = 0;
            state.fixedAnnualPay2 = 0;
            state.qtrFlexFund2 = 0;
            state.totalCTC = 0;

            state.fixedAnnualPay1 = parseFloat(((state.payConsulPerHr1 * (2080 - state.noOfPTO * 8)) * (1 - (state.customFlex1 / 100))) - 4800).toFixed(2)
            state.qtrFlexFund1 = parseFloat(state.payConsulPerHr1 * (2080 - state.noOfPTO * 8) * (state.customFlex1 / 100) / 4).toFixed(2)

            state.fixedAnnualPay2 = parseFloat(state.payConsulPerYr2 * (state.offsetProject2 / 100)).toFixed(2)
            state.qtrFlexFund2 = parseFloat((state.payConsulPerYr2 * (state.offsetProject2 / 100)) / 4).toFixed(2)

            state.totalCTC = parseFloat(parseFloat(state.fixedAnnualPay1) + (parseFloat(state.qtrFlexFund1) * 4) + parseFloat(state.fixedAnnualPay2) + (parseFloat(state.qtrFlexFund2) * 4) + parseFloat(state.medicalPlan) + parseFloat(state.pf)).toFixed(2)

        },

        UPDATE_SALARY_LCA(state) {
            state.LCAQtrFlexFund = 0
            state.LCATotalCTC = 0

            state.LCAQtrFlexFund = parseFloat((state.payConsulPerHr1 * 1920 - state.LCAFixedAnnualPay - 4800) / 4 + state.payConsulPerYr2 / 4).toFixed(2)
            state.LCATotalCTC = parseFloat(parseFloat(state.LCAFixedAnnualPay) + (parseFloat(state.LCAQtrFlexFund) * 4) + parseFloat(state.LCAMedicalPlan) + parseFloat(state.LCAPf)).toFixed(2)
        },

        UPDATE_DATA_TABLE_CONSULT(state) {
            state.dataTableConsult = []
            let qtr = ['Q1 Y1', 'Q2 Y1', 'Q3 Y1', 'Q4 Y1', 'Q1 Y2', 'Q2 Y2', 'Q3 Y2', 'Q4 Y2', 'Q1 Y3', 'Total']
            let bp1 = [408, 500, 500, 500, 500, 500, 500, 500, 256, 4164]
            let ap2 = [200, 250, 300, 200, 300, 250, 350, 200, 100, 2150]
            let ap1 = [400, 480, 500, 520, 480, 500, 480, 400, 300, 4060]
            for (let i = 0; i < 10; i++) {
                let item ={}
                item['qtr'] = qtr[i]
                item['bp1'] = bp1[i]
                item['ap2'] = ap2[i]
                item['ap1'] = ap1[i]
                item['earnings'] = ((ap1[i] * state.payConsulPerHr1) + (ap2[i] * state.payConsulPerHr2)).toFixed(2);
                item['medical'] = 0
                item['pf'] = 0
                item['flex'] = 0
                item['total'] = item['earnings']
                state.dataTableConsult.push(item)

            }
        },

        UPDATE_DATA_TABLE_SALARY(state) {
            state.dataTableSalary = []
            let earningTemp = 0
            let totalTemp = 0
            let flexTemp= 0
            let qtr = ['Q1 Y1', 'Q2 Y1', 'Q3 Y1', 'Q4 Y1', 'Q1 Y2', 'Q2 Y2', 'Q3 Y2', 'Q4 Y2', 'Q1 Y3', 'Total']
            let bp1 = [408, 500, 500, 500, 500, 500, 500, 500, 256, 4164]
            let ap2 = [200, 250, 300, 200, 300, 250, 350, 200, 100, 2150]
            let ap1 = [400, 480, 500, 520, 480, 500, 480, 400, 300, 4060]
            let med = [1000.00, 1200.00, 1200.00, 1200.00, 1200.00, 1200.00, 1200.00, 1200.00, 600.00, 10000.00,]
            let pf = [125.00, 150.00, 150.00, 150.00, 150.00, 150.00, 150.00, 150.00, 75.00, 1250.00,]

            for (let i = 0; i < 10; i++) {
                let item ={}
                item['qtr'] = qtr[i]
                item['bp1'] = bp1[i]
                item['ap2'] = ap2[i]
                item['ap1'] = ap1[i]
                item['earnings'] = ((state.fixedAnnualPay1 + state.fixedAnnualPay2) / 24 * 5).toFixed(2)
                if( i ===0 ){
                    item['earnings'] = parseFloat((parseFloat(state.fixedAnnualPay1) + parseFloat(state.fixedAnnualPay2)) / 24 * 5).toFixed(2)
                    item['flex'] = parseFloat(parseFloat(ap1[i]) * parseFloat(state.payConsulPerHr1) + parseFloat(ap2[i]) * parseFloat(state.payConsulPerHr2) - parseFloat(item['earnings']) - parseFloat(med[i])).toFixed(2)
                    item['total'] = parseFloat(parseFloat(item['earnings']) + parseFloat(item['flex']) + med[i] + pf[i]).toFixed(2)

                }else if(i === 8 ){
                    item['earnings'] = parseFloat((parseFloat(state.fixedAnnualPay1) + parseFloat(state.fixedAnnualPay2)) / 24 * 3).toFixed(2)
                    item['flex'] = parseFloat(parseFloat(ap1[i]) * parseFloat(state.payConsulPerHr1) + parseFloat(ap2[i]) * parseFloat(state.payConsulPerHr2) - parseFloat(item['earnings']) - parseFloat(med[i])).toFixed(2)
                    item['total'] = parseFloat(parseFloat(item['earnings']) + parseFloat(item['flex']) + med[i] + pf[i]).toFixed(2)

                }else if(i===9) {
                    item['earnings'] = earningTemp
                    item['flex'] = flexTemp
                    item['total'] = totalTemp
                }else {
                    item['earnings'] = parseFloat((parseFloat(state.fixedAnnualPay1) + parseFloat(state.fixedAnnualPay2)) / 24 * 6).toFixed(2)
                    item['flex'] = parseFloat(parseFloat(ap1[i]) * parseFloat(state.payConsulPerHr1) + parseFloat(ap2[i]) * parseFloat(state.payConsulPerHr2) - parseFloat(item['earnings']) - parseFloat(med[i])).toFixed(2)
                    item['total'] = parseFloat(parseFloat(item['earnings']) + parseFloat(item['flex']) + med[i] + pf[i]).toFixed(2)

                }
                item['medical'] = med[i]
                item['pf'] = pf[i]

                state.dataTableSalary.push(item)
                earningTemp += parseFloat(item['earnings'])
                flexTemp += parseFloat(item['flex'])
                totalTemp += parseFloat(item['total'])
            }
            state.totalEarningOpt = (parseFloat(state.dataTableSalary[9]['total']) - parseFloat(state.dataTableConsult[9]['total'])).toFixed(2)
            state.earnedOver = (parseFloat(state.dataTableSalary[9]['flex']) - parseFloat(state.dataTableConsult[9]['flex'])).toFixed(2)
            state.allowedNonTax = 20000
        },

        UPDATE_DATA_TABLE_SALARY_LCA(state) {
            state.dataTableSalaryLCA = []
            let earningTemp = 0
            let totalTemp = 0
            let flexTemp= 0

            let qtr = ['Q1 Y1', 'Q2 Y1', 'Q3 Y1', 'Q4 Y1', 'Q1 Y2', 'Q2 Y2', 'Q3 Y2', 'Q4 Y2', 'Q1 Y3', 'Total']
            let bp1 = [408, 500, 500, 500, 500, 500, 500, 500, 256, 4164]
            let ap2 = [200, 250, 300, 200, 300, 250, 350, 200, 100, 2150]
            let ap1 = [400, 480, 500, 520, 480, 500, 480, 400, 300, 4060]
            let med = [1000.00, 1200.00, 1200.00, 1200.00, 1200.00, 1200.00, 1200.00, 1200.00, 600.00, 10000.00,]
            let pf = [125.00, 150.00, 150.00, 150.00, 150.00, 150.00, 150.00, 150.00, 75.00, 1250.00,]

            for (let i = 0; i < 10; i++) {
                let item ={}
                item['qtr'] = qtr[i]
                item['bp1'] = bp1[i]
                item['ap2'] = ap2[i]
                item['ap1'] = ap1[i]
                if( i ===0 ){
                    item['earnings'] = parseFloat(state.LCAFixedAnnualPay / 24 * 5).toFixed(2)
                    item['flex'] = parseFloat(parseFloat(ap1[i]) * parseFloat(state.payConsulPerHr1) + parseFloat(ap2[i]) * parseFloat(state.payConsulPerHr2) - parseFloat(item['earnings']) - parseFloat(med[i])).toFixed(2)
                    item['total'] = parseFloat(parseFloat(item['earnings']) + parseFloat(item['flex']) + med[i] + pf[i]).toFixed(2)

                }else if(i === 8 ){
                    item['earnings'] = parseFloat(state.LCAFixedAnnualPay / 24 * 3).toFixed(2)
                    item['flex'] = parseFloat(parseFloat(ap1[i]) * parseFloat(state.payConsulPerHr1) + parseFloat(ap2[i]) * parseFloat(state.payConsulPerHr2) - parseFloat(item['earnings']) - parseFloat(med[i])).toFixed(2)
                    item['total'] = parseFloat(parseFloat(item['earnings']) + parseFloat(item['flex']) + med[i] + pf[i]).toFixed(2)

                }else if(i===9) {
                    item['earnings'] = earningTemp.toFixed(2)
                    item['flex'] = flexTemp.toFixed(2)
                    item['total'] = totalTemp.toFixed(2)

                }else {
                    item['earnings'] = parseFloat(state.LCAFixedAnnualPay / 24 * 6).toFixed(2)
                    item['flex'] = parseFloat(parseFloat(ap1[i]) * parseFloat(state.payConsulPerHr1) + parseFloat(ap2[i]) * parseFloat(state.payConsulPerHr2) - parseFloat(item['earnings']) - parseFloat(med[i])).toFixed(2)
                    item['total'] = parseFloat(parseFloat(item['earnings']) + parseFloat(item['flex']) + med[i] + pf[i]).toFixed(2)


                }
                item['medical'] = med[i]
                item['pf'] = pf[i]
                state.dataTableSalaryLCA.push(item)
                earningTemp += parseFloat(item['earnings'])
                flexTemp += parseFloat(item['flex'])
                totalTemp += parseFloat(item['total'])


            }
            state.LCATotalEarningOpt = (parseFloat(state.dataTableSalaryLCA[9]['total']) - parseFloat(state.dataTableConsult[9]['total'])).toFixed(2)
            state.LCAEarnedOver = (parseFloat(state.dataTableSalaryLCA[9]['flex']) - parseFloat(state.dataTableConsult[9]['flex'])).toFixed(2)
            state.LCAAllowedNonTax = 20000
        },

        LOGIN(state){
            if(state.username === 'admin' && state.password === 'humetis2020') {
                state.LoggendIn = true
            }
        }


    },

    actions: {
        updateVMSFee({commit}, project) {
            commit('UPDATE_VMS', project)
        },

        updateDOR({commit}, project) {
            commit('UPDATE_DOR', project)
        },

        updateLoad({commit}, project) {
            commit('UPDATE_LOAD', project)
        },
        updatePayRate({commit}, project) {
            commit('UPDATE_PAY_RATE', project)
        },
        updatePayAnnual({commit}, project) {
            commit('UPDATE_PAY_RATE_YR', project)
            commit('UPDATE_SALARY_EMP')
            commit('UPDATE_SALARY_LCA')
            commit('UPDATE_DATA_TABLE_CONSULT')
            commit('UPDATE_DATA_TABLE_SALARY')
            commit('UPDATE_DATA_TABLE_SALARY_LCA')


        },
        updateSalariedEmp({commit}) {
            commit('UPDATE_SALARY_EMP')
        },

        updateSalaryLCA({commit}) {
            commit('UPDATE_SALARY_LCA')
        },

        updateDataTableConsult({commit}) {
            commit('UPDATE_DATA_TABLE_CONSULT')
        },

        login({commit}) {
            commit('LOGIN')
        }

    }
})